.login {
    position: relative;
    margin: 0 auto;
    width: 400px;
    height: 300px;
}

.login_img {
    margin-top: 15px;
    margin: 0 auto;
    width: 350px;
    height: 250px;
    display: inline-block;
    border: 1px solid rgb(255, 255, 255);
    border-radius: 10px;
}

.login_text {
    width: 290px;
    margin-left: 23px;
    margin-bottom: 20px;
    color: rgb(219, 10, 10);
    text-align: center;
    font-weight: bold;
    top: 10px;
}

.login_form_block{
    width: 250px;
    position: absolute;
    bottom: -5px;
    left: 53px;
}

.login_form {
    position: relative;
    display: flex;
    flex-direction: column;
}

.login_form input {
    padding-left: 5px;
    padding-right: 5px;
    height: 30px;
    text-align: center;
    margin-bottom: 2px;
    margin-top: 2px;
    margin-left: 35px;
    border: 1px solid rgb(0, 0, 0);
    border-radius: 10px;
    background-color: rgb(211, 163, 6);
    color: white;
}

.login_form_checkbox {
    width: 16px;
    cursor: pointer;
}

.login_form h2 {
    position: absolute;
    line-height: 10px;
    font-size: 17px;
    font-weight: 600;
    bottom: 27px;
    left: 55px;
} 

.login_form button {
    border: 1px solid rgb(0, 0, 0);
    border-radius: 10px;
    height: 25px;
    font-weight: bold;
    background-color: rgb(110, 184, 187);
    cursor: pointer;
    transition: all 0.3s;
}

.login_form button:hover {
    background-color:  rgb(46, 184, 189);
}

.login_form input::-webkit-input-placeholder {
    color: black;
    font-weight: 400;
    opacity: 0.5;
}

.login_form span {
    font-size: 10px;
    width: 110px;
    margin-left: 60px;
    border-radius: 10px
}

.login_form_error {
    text-align: center;
    border: 1px solid rgb(0, 0, 0);
    border-radius: 10px;
    background-color: red;
    font-weight: 400;
    font-size: 15px;
    width: 200px;
    margin:  0 auto;
    margin-left: 15px;
    margin-bottom: 10px;
}

